import React from "react";
//Alow rotating logo
import "./rotate.css";
//Alow scrollable columns
import "./scroll.css";
//Alow disapearing text
import "./disappear.css";
//Activate jQuery
import $ from "jquery";

import Messages from "./messages.jsx";
import InitialComponents from "./components";

const MainClock = require("./images/clock.gif");

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      bits: 0,
      totalBits: 0,
      flops: 0,

      logicGates: 0,
      microControlers: 0,
      crystals: 0,

      Components: InitialComponents,

      RedDiode: {
        activated: false,
        description: "Increase your flops by 5%",
        img: require("./images/red-diode.png")
      },
      BlueDiode: {
        activated: false,
        description: "I'm blue da ba dee da ba daa",
        img: require("./images/blue-diode.png")
      },
      Turbo: {
        activated: false,
        description: "Increase your flops by 25%",
        img: require("./images/turbo.png")
      }
    };

    // Cycle operator button
    this.executeOperation = this.executeOperation.bind(this);

    // Buy buttons
    this.buyItem = this.buyItem.bind(this);

    // Update informations
    this.computeFlops = this.computeFlops.bind(this);
    this.computePrice = this.computePrice.bind(this);
    this.updateFlops = this.updateFlops.bind(this);
    this.runGameLoop = this.runGameLoop.bind(this);

    // Display nice messages
    this.writeMessages = this.writeMessages.bind(this);
    this.alertBox = this.alertBox.bind(this);
    this.unknownButton = this.unknownButton.bind(this);
    this.createButtons = this.createButtons.bind(this);
    this.createButton = this.createButton.bind(this);
    this.displayDiodes = this.displayDiodes.bind(this);
    this.displayDiode = this.displayDiode.bind(this);

    // Interval update (100ms)
    setInterval(this.runGameLoop, 100);
  }

  computeBitsPerClick(state) {
    return parseInt(
      1 +
        state.Components.Transistor.number / 10 +
        state.flops * 0.1 +
        state.flops *
          (Math.pow(1.1, state.Components.RamExtension.number) - 1) +
        (state.RedDiode.activated ? state.flops * 0.15 : 0)
    );
  }

  // Execute one cycle of the computer
  executeOperation() {
    this.setState(state => ({
      bits: state.bits + this.computeBitsPerClick(state),
      totalBits: state.totalBits + this.computeBitsPerClick(state)
    }));
    this.updateFlops();

    // Animate button
    $(".rotate").toggleClass("circle");
    $(".trensparent").css("opacity", 1);
    $(".trensparent").stop(true, true);
    $(".trensparent").fadeIn(0);
    $(".trensparent").fadeOut();
  }

  // Compute the price of a component using the number
  // of items bought.
  computePrice(component) {
    return Math.floor(
      component.price * Math.pow(1 + component.priceIncrease, component.number)
    );
  }

  //Buy a component
  buyItem(componentName) {
    this.setState(state => ({
      //Spend bits to buy the component
      bits: state.bits - this.computePrice(state.Components[componentName]),
      //Update number of components
      Components: {
        ...state.Components,
        [componentName]: {
          ...state.Components[componentName],
          ...{
            number: state.Components[componentName].number + 1
          }
        }
      }
    }));
    this.updateFlops();
  }

  // Update flops
  updateFlops() {
    // Update Floating point operation per seconds
    this.setState(state => ({
      flops: this.computeFlops(state)
    }));

    // For each component, update if it is disabled or not
    // depending on the amount of bit available.
    for (let componentName in this.state.Components) {
      this.setState(state => ({
        Components: {
          ...state.Components,
          [componentName]: {
            ...state.Components[componentName],
            ...{
              disabled:
                state.bits >= this.computePrice(state.Components[componentName])
                  ? false
                  : true
            }
          }
        }
      }));
    }
  }

  // Compute current FLOPS from components owned by the user.
  computeFlops(state) {
    var flops = 0;

    for (let componentName in state.Components) {
      flops +=
        state.Components[componentName].number *
        state.Components[componentName].flops;
    }

    // Add Ram Extension boost
    flops *= Math.pow(1.1, state.Components.RamExtension.number);

    // Add Red Diod
    if (this.state.RedDiode.activated) flops *= 1.05;

    // Add Turbo
    if (this.state.Turbo.activated) flops *= 1.25;

    return flops;
  }

  // Run Game Loop (called every 1/10s)
  runGameLoop() {
    // Update flops
    this.updateFlops();

    // increase amount of bits
    this.setState(state => ({
      bits: state.bits + state.flops * (1.0 / 10),
      totalBits: state.totalBits + state.flops * (1.0 / 10)
    }));

    for (let componentName in this.state.Components) {
      if (
        this.state.bits >=
        (2.0 / 3.0) * this.state.Components[componentName].price
      )
        this.setState(state => ({
          Components: {
            ...state.Components,
            [componentName]: {
              ...state.Components[componentName],
              ...{ discovered: true }
            }
          }
        }));
    }
  }

  createButton(name) {
    // Create the buy function with the component name binded
    var buyFct = this.buyItem.bind(this, name);
    const btnAction = function() {
      $('[data-toggle="tooltip"]').tooltip("hide");
      buyFct();
    };
    // Get the component from its name
    const component = this.state.Components[name];
    // If the component is discovered, show it
    if (component.discovered)
      return (
        <button
          class="btn btn-success text-left btn-block"
          disabled={component.disabled}
          onClick={btnAction}
          data-toggle="tooltip"
          data-placement="top"
          title={component.description}
        >
          <div class="row">
            <div class="col-sm-3 my-auto">
              <img
                src={component.img}
                class="img-thumbnail"
                alt=""
                width="100px"
              />
            </div>
            <div class="col-sm-9 my-auto">
              <div class="row">
                <div class="col">
                  {component.name} &nbsp;
                  <span class="badge badge-secondary">{component.number}</span>
                </div>
              </div>
              <div class="row">
                <div class="col text-left">
                  {this.humanReadableBits(this.computePrice(component))}
                </div>
                <div class="col text-right">({component.flops} flops)</div>
              </div>
            </div>
          </div>
        </button>
      );
    // Otherwise show nothing
    else return <div />;
  }

  alertBox(msg) {
    return (
      <div class="alert alert-info alert-dismissible">
        <button type="button" class="close" data-dismiss="alert">
          &times;
        </button>
        {msg}
      </div>
    );
  }

  // Take a number of bits and display it as a nice
  // human readable string.
  humanReadableBits(bits) {
    bits = Math.floor(bits);
    function f(number) {
      return Math.floor(1000 * number) / 1000;
    }
    if (bits < 1000) return f(bits) + " bits";
    bits = bits / 1000;
    if (bits < 1000) return f(bits) + " kilobits";
    bits = bits / 1000;
    if (bits < 1000) return f(bits) + " megabits";
    bits = bits / 1000;
    if (bits < 1000) return f(bits) + " gigabits";
    bits = bits / 1000;
    if (bits < 1000) return f(bits) + " terabits";
    bits = bits / 1000;
    if (bits < 1000) return f(bits) + " petabits";
    bits = bits / 1000;
    if (bits < 1000) return f(bits) + " zettabits";
    bits = bits / 1000;
    return f(bits) + " yottabits";
  }

  writeMessages() {
    var htmlOut = [];
    for (const entry of Messages) {
      if (this.state.totalBits >= entry.triggerOps)
        htmlOut.push(this.alertBox(entry.message));
    }
    return htmlOut;
  }

  createButtons() {
    var buttons = [];
    for (let componentName in this.state.Components)
      buttons.push(this.createButton(componentName));
    return buttons;
  }

  unknownButton() {
    var everythingDiscovered = true;
    for (let componentName in this.state.Components) {
      everythingDiscovered &= this.state.Components[componentName].discovered;
    }
    if (!everythingDiscovered)
      return (
        <button class="btn btn-second btn-block" disabled={true}>
          <b>? ? ?</b>
        </button>
      );
    return <div />;
  }

  // Easter Eggs :)
  displayDiode(name) {
    let addDiode = () => {
      // Update the diode state
      this.setState(state => ({
        [name]: {
          ...state[name],
          ...{ activated: true }
        }
      }));
      // Hide the tooltip
      $('[data-toggle="tooltip"]').tooltip("hide");
    };

    return (
      <img
        id={name}
        alt="Diode"
        class="img-thumbnail"
        src={this.state[name].img}
        width="30px"
        data-toggle="tooltip"
        data-placement="bottom"
        title={this.state[name].description}
        onClick={addDiode}
      />
    );
  }
  displayDiodes() {
    // Output display
    var htmlOut = [];

    // Show a red diode if not already collected
    if (
      this.state.Components.Transistor.number === 42 &&
      !this.state.RedDiode.activated
    )
      htmlOut.push(this.displayDiode("RedDiode"));

    // Show a red diode if not already collected
    if (
      this.state.flops <= 0.001 &&
      this.state.bits >= 1000 &&
      !this.state.BlueDiode.activated
    )
      htmlOut.push(this.displayDiode("BlueDiode"));

    // Show a turbo if not already collected
    if (
      this.state.Components.Transistor.number === 0 &&
      this.state.Components.LogicGate.number === 1 &&
      !this.state.Turbo.activated
    )
      htmlOut.push(this.displayDiode("Turbo"));
    return htmlOut;
  }

  showCapturedDiode(name) {
    return (
      <img
        alt={name}
        class="img-thumbnail "
        src={this.state[name].img}
        width="30px"
        data-toggle="tooltip"
        data-placement="top"
        title={this.state[name].description}
        data-original-title={this.state[name].description}
      />
    );
  }
  showCapturedDiodes() {
    var htmlOut = [];

    if (this.state.BlueDiode.activated)
      htmlOut.push(this.showCapturedDiode("BlueDiode"));

    if (this.state.RedDiode.activated)
      htmlOut.push(this.showCapturedDiode("RedDiode"));

    if (this.state.Turbo.activated)
      htmlOut.push(this.showCapturedDiode("Turbo"));

    if (htmlOut.length > 0) htmlOut.push(<br />);
    return htmlOut;
  }

  render() {
    // initialise tooltips
    $(function() {
      $('[data-toggle="tooltip"]').tooltip();
    });
    return (
      <div class="container">
        <div class="row">
          <div class="col-sm-4 text-center">
            {this.showCapturedDiodes()}
            <h2 class="text-center">
              {this.humanReadableBits(this.state.bits)}
            </h2>
            <h3 class="badge badge-secondary">
              Floating Points Operation Per Seconds :{" "}
              {this.state.flops.toFixed(2)}
            </h3>
            <br />
            <img
              src={MainClock}
              onClick={this.executeOperation}
              class="rotate img-thumbnail"
              alt="Activate the main clock"
            />
            <br />
            <span class="trensparent badge badge-pill badge-dark">
              + {this.computeBitsPerClick(this.state)}
              &nbsp;bits
            </span>
            <br />
            {this.displayDiodes()}
          </div>
          <div class="col-sm-4">{this.writeMessages()}</div>
          <div class="col-sm-4">
            <div class="scroll-zone container-fluid">
              {this.createButtons()}
              <br />
              {this.unknownButton()}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default App;
